body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
    font-family: 'SEGA LOGO FONT';
    font-style: normal;
    font-weight: normal;
    src: local('SEGA LOGO FONT'), url(/robo-stars-war/static/media/SEGA.12a3db8b.woff) format('woff');
}

@font-face {font-family:"STARWARS";src:url(/robo-stars-war/static/media/STARWARS.35f31c7a.woff) format("woff");font-weight:normal;font-style:normal;}



body{
    background: url(/robo-stars-war/static/media/star-background-star-wars-7.6f6f9adb.jpg);
    background-size: cover;
}

h1{
    font-family: 'STARWARS';
    color: yellow;
    -webkit-transform: skew(-45deg);
            transform: skew(-45deg);
    width: 500px;
}

header{
    width: 500px;
    margin: 0 auto;
}
